import * as constants from './constants';
import { api } from '../../../api/config';
import { message } from 'antd';
import axios from 'axios';

export const getChannelList = () => {
  return (dispatch) => {
    axios.get(api + '/distribution/all')
      .then(res => {
        if(res.data.success === '成功') {
          let list = res.data.data;
          const action = {
            type: constants.GET_CHANNEL_LIST,
            list
          };
          dispatch(action);
        }
      })
  }
};

export const handleDeleteItem = (list, record, index) => {
  return (dispatch) => {
    axios.get(api + '/distribution/delete', {params: {id: record._id}})
      .then(res => {
        if(res.data.success === '成功') {
          list.splice(index, 1);
          message.success('删除成功');
          const action = {
            type: constants.DELETE_ITEM,
            list
          };
          dispatch(action);
        }
      })
  }
};

export const changeChannelInput = (title) => ({
  type: constants.CHANGE_CHANNEL_INPUT,
  title
});

export const changeGonghaoInput = (name) => ({
  type: constants.CHANGE_GONGHAO_INPUT,
  name
});

export const uploadImage = (image) => ({
  type: constants.UPLOAD_IMAGE,
  image
});

export const handleCreate = (props) => {
  return (dispatch) => {
    axios.post(api + '/distribution/create', {
      title: props.title,
      official: props.official
    }).then(res => {
      if(res.data.success === '成功') {
        message.success("创建成功");
        const action = {
          type: constants.CREATE_FORM
        };
        dispatch(action);
        props.history.goBack();
      }
    })
  }
};

export const initState = (data) => ({
  type: constants.INIT_STATE,
  data
});

export const handleUpdate = (props) => {
  return (dispatch) => {
    axios.post(api + '/distribution/update', {
      id: props.id,
      title: props.title,
      official: props.official
    }).then(res => {
      if(res.data.success === '成功') {
        message.success("修改成功");
        const action = {
          type: constants.UPDATE_FORM
        };
        dispatch(action);
        props.history.goBack();
      }
    })
  }
};

export const getAdzones = (id) => {
  return (dispatch) => {
    axios.get(api + '/distribution/adzones', {
      params: {
        id: id
      }
    }).then(res => {
        if(res.data.success === '成功') {
          let data = res.data.data;
          const action = {
            type: constants.GET_ADZONES_INFO,
            data
          };
          dispatch(action);
        }
      })
  }
};

