import * as constants from './constants';
import axios from "axios";
import {message} from "antd";
import { api } from '../../../api/config';

// 获取小说列表
export const getNovelList = () => {
  return (dispatch) => {
    axios.get(api + '/book/all').then(res => {
      let list = res.data.data;
      const action = {
        type: constants.GET_NOVEL_LIST,
        list
      };
      dispatch(action)
    })
  }
};

export const getNovelInfo = (novelInfo) => {
  return (dispatch) => {
    const action = {
      type: constants.GET_NOVEL_INFO,
      novelInfo
    };
    dispatch(action)
  }
};

export const handleInputChange = (page_title) => ({
  type: constants.CHANGE_INPUT,
  page_title
});

export const handleChange = (image_url) => ({
  type: constants.UPLOAD_IMAGE,
  image_url
});

export const handleSubmit = (props) => {
  return (dispatch) => {
    axios.post(api + '/book/update', {
      id: props._id,
      page_title: props.page_title,
      image_url: props.image_url
    }).then(res => {
      if(res.data.success === '成功') {
        message.success('修改成功');
        const action = {
          type: constants.SUBMIT_FORM
        };
        dispatch(action);
        props.history.goBack()
      }
    })
  }
};

export const setBookInfo = (id, title) => ({
  type: constants.SET_BOOK_INFO,
  id, title
});

export const getBookRules = (id) => {
  return dispatch => {
    axios.get(api + '/rule', {params: {id: id}})
      .then(res => {
        if(res.data.success === '成功') {
          let rules = res.data.data;
          let chapterCount = res.data.chapter_count;
          const action = {
            type: constants.GET_BOOK_RULES,
            rules, chapterCount
          };
          dispatch(action)
        }
      })
  }
};

export const handlePriceChange = (price) => ({
  type: constants.CHANGE_PRICE_INPUT,
  price
});

export const handleStartChange = (start) => ({
  type: constants.CHANGE_START_INPUT,
  start
});

export const handleEndChange = (end) => ({
  type: constants.CHANGE_END_INPUT,
  end
});

export const submitRules = (props) => {
  return dispatch => {
    axios.post(api + '/rule/create', {
      bid: Number(props.book.id),
      start: Number(props.start),
      end: Number(props.end),
      price: Number(props.price)
    }).then(res => {
      if(res.data.success === '成功') {
        message.success('创建成功');
        props.history.goBack();
      }
    })
  }
};

export const handleDeleteItem = (record, index) => {
  return dispatch => {
    axios.get(api + '/rule/delete', {params: {id: record._id}})
      .then(res => {
        if(res.data.success === '成功') {
          const action = {
            type: constants.DELETE_ITEM,
            index
          };
          dispatch(action);
          message.success('删除成功');
        } else {
          message.error('删除失败');
        }
      })
  }
};

export const resetState = () => ({
  type: constants.RESET_STATE
});



