import * as constants from './constants';

const defaultState = {
  list: [],
  page_title: '',
  image_url: '',
  title: '',
  _id: '',
  rules: [],
  book: {
    id: '',
    title: ''
  },
  price: null,
  start: null,
  end: null,
  chapterCount: null
};

export default (state = defaultState, action) => {
  let newState = JSON.parse(JSON.stringify(state));
  switch (action.type) {
    case constants.GET_NOVEL_LIST:
      newState.list = action.list;
      return newState;
    case constants.GET_NOVEL_INFO:
      newState.page_title = action.novelInfo.page_title;
      newState.image_url = action.novelInfo.image_url;
      newState.title = action.novelInfo.title;
      newState._id = action.novelInfo._id;
      return newState;
    case constants.CHANGE_INPUT:
      newState.page_title = action.page_title;
      return newState;
    case constants.UPLOAD_IMAGE:
      newState.image_url = action.image_url;
      return newState;
    case constants.SET_BOOK_INFO:
      newState.book = {
        id: action.id,
        title: action.title
      };
      return newState;
    case constants.GET_BOOK_RULES:
      newState.rules = [...action.rules];
      newState.chapterCount = action.chapterCount;
      return newState;
    case constants.CHANGE_PRICE_INPUT:
      newState.price = action.price;
      return newState;
    case constants.CHANGE_START_INPUT:
      newState.start = action.start;
      return newState;
    case constants.CHANGE_END_INPUT:
      newState.end = action.end;
      return newState;
    case constants.DELETE_ITEM:
      newState.rules.splice(action.index, 1);
      return newState;
    case constants.RESET_STATE:
      return defaultState;
    default:
      return state;
  }
};