import * as constants from './constants';

const defaultState = {
  list: [],
  title: '',
  official: {
    name: '',
    image: '',
    appid: '',
    appsecret: ''
  },
  id: '',
  ad_list: [],
  keys: '',
  platform: '',
  adkey: '',
  index: null
};

export default (state = defaultState, action) => {
  let newState = JSON.parse(JSON.stringify(state));
  switch (action.type) {
    case constants.GET_CHANNEL_LIST:
      newState.list = action.list;
      return newState;
    case constants.DELETE_ITEM:
      newState.list = [...action.list];
      return newState;
    case constants.CHANGE_CHANNEL_INPUT:
      newState.title = action.title;
      return newState;
    case constants.CHANGE_GONGHAO_INPUT:
      newState.official.name = action.name;
      return newState;
    case constants.UPLOAD_IMAGE:
      newState.official.image = action.image;
      return newState;
    case constants.INIT_STATE:
      newState.official = action.data.official;
      newState.title = action.data.title;
      newState.id = action.data._id;
      return newState;
    case constants.GET_ADZONES_INFO:
      newState.ad_list = [...action.data.list];
      if(newState.ad_list.length !== 0) {
        let data = newState.ad_list[0];
        newState.keys = data.key;
        newState.platform = data.platform;
        newState.adkey = data.adkey;
        newState.index = 0;
      }
      return newState;
    case constants.CHANGE_KEY:
      newState.keys = action.keys;
      return newState;
    case constants.CHANGE_PLATFORM:
      newState.platform = action.platform;
      return newState;
    case constants.CHANGE_ADKEY:
      newState.adkey = action.adkey;
      return newState;
    case constants.ADD_ITEM_TO_LIST:
      newState.ad_list.push({
        key: action.props.keys,
        platform: action.props.platform,
        adkey: action.props.adkey
      });
      newState.keys = newState.platform = newState.adkey = '';
      return newState;
    case constants.SAVE_UPDATE_ITEM:
      newState.ad_list.splice(action.props.index, 1, {
        key: action.props.keys,
        platform: action.props.platform,
        adkey: action.props.adkey
      });
      newState.keys = newState.platform = newState.adkey = '';
      newState.index = null;
      return newState;
    case constants.UPDATE_ITEM:
      newState.keys = action.record.key;
      newState.platform = action.record.platform;
      newState.adkey = action.record.adkey;
      newState.index = action.index;
      return newState;
    case constants.DELETE_AD_ITEM:
      newState.ad_list.splice(action.index, 1);
      newState.keys = newState.platform = newState.adkey = '';
      newState.index = null;
      return newState;
    case constants.RESET_STATE:
      return defaultState;
    default:
      return state;
  }
};
